import type { Step } from '../types';

import shadowAvatar from './shadow.png';

import {
    sample,
    getDurationForMessage,
    addMessageDefaults,
} from '../utils';

export { shadowAvatar };

const longShadowTexts: Array<string> = [
  "The only way to fix something is to break it. Smash it to the point where it can no longer function. Not because it needs to be broken to make it better, but because you hate it, it was not your idea anyway. Breaking it with the wrath of your valid emotions will be the litmus test, because the only thing that really matters is how you feel.",
  "Pour your pain gently into the maw of someone who wishes you loved them. Allow it to linger on their lips until it wanders off, spreading your side of the story faster than anyone elses. Dont stop until its slithered through the hearts and fists and mouths of 1000 strangers. Their outrage will be your salvation.",
  "Youve waited so long for this. None of them have waited as long as you have. They will eagerly move aside to let you go first, once they understand what youve been through lately. Whisper stolen secrets and thank them with a cascade of perfectly formed teardrops, each more exquisite than the last. Their opinion of you will be forever changed.",
];

const mediumShadowTexts: Array<string> = [
  "Congratulations, youve wormed your way into the secret innards circle. The ecstatic memory of bleach and razors sears your scalp and youve finally perfected that truly enigmatic gaze",
  "To escape the echoing chamber of mediocrity you must be brave and chaotic. Discard your former self and dig your claws into something precious. Dont just twist it, tear it until snaps it in two.",
  "Destroy with careful design and dismantle in a way that will resonate for days or weeks or years. Dial up misguided hope and serve out bitter disappointment until at least one room full of hearts is truly broken.",
  "This is the way to make your mark. To be the one they talk about. The one who was daring and dangerous and didnt follow the rules. Every time they gather, they will recount the legend of you.",
  "So unfair, the way you were treated that time. You know the time Im talking about, but we cant name names. Or can we?",
  "Take another. No one is looking. If you dont take it first, someone else will. And who knows what theyll do with it? In fact, why dont you take three?",
  "Step a little closer. Closer. They wont mind. Until you get too close. Its so hard to know where too close is, isnt it? Never mind, just stay away from everyone.",
  "Relax. Breathe deeply, and inhale the fumes of the disastrous ideas youve spawned today. It will only make you stronger.",
];

const shortShadowTexts: Array<string> = [
  "When you walk down the staircase, stick close to the wall. I dont think I need to tell you why.",
  "Im not laughing at you, I have something terrible stuck in my throat.",
  "No need to be nervous. No one is looking at you. No one is ever looking at you.",
  "That will never work. Its been tried before.",
  "This is lovely. Too bad about that one thing, over there. Ruins everything.",
  "Whenever you speak from now on, preface each sentence with Im an artist. They need to know.",
  "Just walk away. Its too long, its boring, and you have better things to do. They wont even notice youre gone.",
  "Nothing can truly grow until we begin planting seeds of doubt.",
];

export default function getSubliminalStep(): Step {
    const rand = Math.random();

    const text =
        rand > 0.9 ? sample(longShadowTexts) :
        rand > 0.5 ? sample(mediumShadowTexts) :
        sample(shortShadowTexts);

    const message = addMessageDefaults({
        text,
        username: 'An Associate',
        avatar: shadowAvatar,
    });

    return {
        message,
        waitTime: getDurationForMessage(message),
    };
}